/**
 * Template for isgi
 */
 <i18n>
{
   "en":{
        "isgi": "International Service of Geomagnetic Indices",
        "description":"<b>ISGI</b> is the Reference service of the International Association of Geomagnetism and Aeronomy (IAGA) for validation, dissemination and stewardship of geomagnetic indices (with the help of 6 ISGI-Collaborating-Institutes internationally spread) through its official Web portal. <br /><br /><b>ISGI</b> is an official French National Service of <b>CNRS-INSU</b> which is getting a double label, both in &laquo;Solid Earth&raquo; <b>TSANO4</b> (National Observation in Magnetism) and in &laquo;Astronomy-Astrophysics&raquo; <b>AAANO6</b> (Surveillance of the Sun and the Earth's Space Environment)."
      
   },
   "fr":{
         "isgi": "Service International des Indices Gomagntiques",
          "description":"<b>ISGI</b> est le service de rfrence de l'Association Internationale de Gomagntisme et Aronomie (IAGA) pour la validation, la diffusion et la gestion des indices gomagntiques (avec la collaboration de 6 Instituts Internationaux)  travers leur portail web. <br /><br /><b>ISGI</b> est le service officiel franais du <b>CNRS-INSU</b> disposant d'une double tiquette,  la fois prsent dans le &laquo;Ple Terre Solide&raquo; <b>TSANO4</b> (Observation National en Magntisme) et dans  &laquo;Astronomie-Astrophysiques&raquo; <b>AAANO6</b> (Surveillance du Soleil et de l'Espace Environnant la Terre)."
      
   }
}
</i18n>
<template>
<span class="isgi-layout">
  <header>
    <div>
    <a href="http://isgi.unistra.fr" :title="$t('isgi')">
    <img class="isgi-logo" src="http://isgi.unistra.fr/Images/logo_ISGI.gif" alt="ISGI"/>
    </a>
    </div>
    <div class="isgi-description" v-html="$t('description')">
    
    </div>
    </header>
    <formater-layout>
    
    <div class="formater-wrapper">
        <aside>
            <isgi-form :lang="lang" :indices="indices"></isgi-form>   
        </aside>
        <main>
            <isgi-chart :lang="lang" v-for="(indice, id) in indices" :indice="indice" :key="id" :width="mainWidth"  :id="id" ></isgi-chart>
        </main>
         </div>
        
     <footer>
    </footer>
    </formater-layout>
   
  
  
    
    </span>
</template>
<script>
export default {
	 props:{
	        lang: {
	            type:String,
	            default:'fr'
	        }
	},
	data(){
		return{
			indices: ['aa', 'am', 'Kp', 'Dst', 'PC', 'AE', 'Qdays', 'SC'/*, 'SFE', 'Qdays', 'CKdays'*/],
			windowResizeListener: null,
			aerisThemeListener:null,
			mainWidth:300
		}
	},
	methods:{
		emitMainWidth(){
			this.computeMainWidth();
			var event = new CustomEvent('isgiResize', { detail:{mainWidth:this.mainWidth}});
			document.dispatchEvent(event);
		},
		computeMainWidth(){
			var width = this.$el.querySelector(".formater-wrapper").offsetWidth;
	    	width -= this.$el.querySelector("aside").offsetWidth;

	        this.mainWidth = width;
			
			 return width;
		},
		addWidth( evt){
			evt.detail.mainWidth = this.computeMainWidth();
			
		}
	},
//     computed:{
//     	mainWidth(){
//     		var left = this.$el.querySelector("aside").offsetWidth;
//     		return "width: 300px;";
//     	}
//     },
    created(){
        this.$i18n.locale = this.lang;
        this.windowResizeListener = this.emitMainWidth.bind( this);
        window.addEventListener('resize', this.windowResizeListener);
        this.aerisThemeListener = this.addWidth.bind( this);
        document.addEventListener('aerisTheme', this.aerisThemeListener);
        if( isgi ) this.indices = isgi.indices();
        
    },
    mounted(){
    	this.computeMainWidth();
    	
    },
    destroyed(){
    	 window.removeEventListener( 'resize', this.windowResizeListener);
         this.windowResizeListener = null;
         document.removeAerisThemeListener('aerisTheme', this.aerisThemeListener);
         this.aerisThemeListener =null
    }
}
</script>
<style>
.isgi-layout > header{
    background-color:#fff;
    display: block;
	-webkit-box-flex: 1;
	-ms-flex: 1 1 auto;
	flex: 1 1 auto;
	    flex-grow: 1;
	    flex-shrink: 1;
	    flex-basis: auto;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-ms-flex-direction: row;
	flex-direction: row;
	vertical-align:bottom;
}
.isgi-layout > header img.isgi-logo{
    width:200px;
}
.isgi-layout > header div{
    display: block;
    vertical-align: bottom;
}
.isgi-layout > header div.isgi-description{
    margin-top:80px;
    margin-left:20px;
    }
</style>


